                            <td v-text=\"list.companyIntnet\"></td>\r\n                            <td v-text=\"list.roomIntnet\"></td>\r\n                            <td v-text=\"list.roomOutnet\"></td>\r\n                            <td v-text=\"list.room\"></td>\r\n                            <td>\r\n                                <button type=\"button\" class=\"btn btn-default\" @click=\"unbind(list.id)\">解绑</button>\r\n                            </td>\r\n                        </tr>\r\n                        <tr class=\"text-center\" v-show=\"tableList.length === 0\">\r\n                            <td :colspan=\"7\">暂无数据</td>\r\n                        </tr>\r\n                    </tbody>\r\n                    <tfoot>\r\n                        <tr>\r\n                            <td :colspan=\"7\">\r\n                                <boot-page v-ref:page :async=\"true\" :lens=\"lenArr\" :page-len=\"pageLen\" :url=\"url\" :param=\"param\"></boot-page>\r\n                            </td>\r\n                        </tr>\r\n                    </tfoot>\r\n                </table>\r\n                <spinner id=\"spinner-box\" :size=\"md\" :fixed=\"false\" \r\n                     text=\"数据加载中，请稍后...\" v-ref:spinner>\r\n                </spinner>\r\n            </div>\r\n        </div>\r\n        <div slot=\"modal-footer\" class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-default\" @click='viewServerModal = false'>取消</button>\r\n        </div>\r\n    </modal>\r\n</template>\r\n\r\n<script>\r\nimport { modal } from 'vue-strap'\r\nimport bootPage from '../../global/BootPage.vue'\r\n\r\nexport default {\r\n    data() {\r\n        return {\r\n            showData: false,\r\n            viewServerModal: false,\r\n            param: {\r\n                id: null,\r\n                sn: '',\r\n                type: 8\r\n            },\r\n            tableList: [],\r\n            lenArr: [10, 50, 100],\r\n            pageLen: 5,\r\n            url: '/group/detail/',\r\n        }\r\n    },\r\n    methods: {\r\n\r\n        // 刷新数据\r\n        refresh () {\r\n            this.$refs.page.refresh()\r\n        },\r\n\r\n        // 解绑\r\n        unbind (id) {\r\n            this.$http({\r\n                url: '/group/unbind_machines/',\r\n                method: 'POST',\r\n                data: {\r\n                    id: this.param.id,\r\n                    checkedIds: [id]\r\n                }\r\n            })\r\n            .then(response => {\r\n                if (response.data.code === 200) {\r\n                    this.$dispatch('refresh')\r\n                    this.$dispatch('show-success')\r\n                } else {\r\n                    this.$dispatch('show-error', response.data.msg)\r\n                }\r\n            })\r\n        }\r\n    },\r\n    ready () {\r\n        \r\n    },\r\n    components: {\r\n        modal,\r\n        bootPage\r\n    },\r\n    events: {\r\n        'showServerModal' (id) {\r\n            this.viewServerModal = true\r\n\r\n            this.param.id = id\r\n            this.tableList = []\r\n            this.showData = true\r\n\r\n            this.refresh()\r\n        },\r\n\r\n        // 获取表格数据\r\n        'data' (param) {\r\n            this.tableList = param.data\r\n        },\r\n\r\n        // 刷新表格\r\n        'refresh' () {\r\n            this.refresh()\r\n        },\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.max-height {\r\n    max-height: 550px;\r\n    overflow: auto;\r\n}\r\n</style>\r\n\r\n"],"sourceRoot":"webpack://"}]);

